<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
    <div class="container-fluid">
      <span class="header-span">
        Добро пожаловать! {{ username }}
      </span>
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDarkDropdown"
        aria-controls="navbarNavDarkDropdown" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNavDarkDropdown">
        <ul class="navbar-nav">
          <li class="nav-item dropdown">
            <button class="btn btn-dark dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
              Все ссылки
            </button>
            <ul class="dropdown-menu dropdown-menu-dark">
              <li class="login">
                <RouterLink to="/">Главная</RouterLink>
              </li>
              <li class="login">
                <RouterLink to="/about">О нас</RouterLink>
              </li>
            </ul>
          </li>
        </ul>

        <ul class="navbar-nav" v-if="username === null">
          <li class="nav-item dropdown">
            <button class="btn btn-dark dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
              Аутентификация
            </button>
            <ul class="dropdown-menu dropdown-menu-dark">
              <li class="login">
                <RouterLink to="/login">Войти</RouterLink>
              </li>
              <li class="login">
                <RouterLink to="/register">Зарегистрироваться</RouterLink>
              </li>
            </ul>
          </li>
        </ul>

        <ul class="navbar-nav" v-else>
          <li class="nav-item dropdown">
            <button class="btn btn-outline-danger" @click="logout">
              Выйти
            </button>
          </li>
        </ul>


      </div>
    </div>
  </nav>
</template>

<script>
import axios from 'axios';
import { jwtDecode, InvalidTokenError } from 'jwt-decode';
import { authStore } from "@/store/store";
import { ref, inject } from 'vue';

export default {
  setup() {
    // Используем inject для доступа к реактивной переменной из хранилища
    const username = inject('authStore').username || ref(null);

    // Возвращаем данные из компонента
    return { username };
  },
  async mounted() {
    // Вызывайте функцию обновления токена каждые 30 минут
    setInterval(() => this.refreshToken(localStorage.getItem('refreshToken')), 30 * 60 * 1000);

    // Получаем токен из localStorage
    if (localStorage.getItem('accessToken')) {
      var accessToken = localStorage.getItem('accessToken');
    }

    // Декодируем токен и получаем id пользователя
    const tokenPayload = this.parseJwt(accessToken);
    // console.log(tokenPayload);
    if (tokenPayload) {
      try {
        const response = await axios.get(`http://127.0.0.1:8000/login/${tokenPayload.user_id}`, {
          headers: {
            Authorization: `Bearer ${accessToken}`
          }
        });
        // console.log(response.data);
        this.username = response.data.username;
        console.log(this.username);
      } catch (error) {
        this.username = null;
        console.error(error);
      }
    }

  },

  methods: {
    parseJwt(token) {
      try {
        const decoded = jwtDecode(token);
        return decoded;
      } catch (error) {
        console.log('Error decoding JWT:', error);
        return null;
      }
    },

    async refreshToken(token) {
      try {
        if (localStorage.getItem('accessToken')) {
          localStorage.removeItem('accessToken');
        } else {
          // Используйте await, чтобы дождаться завершения запроса
          const response = await axios.post(
            'http://127.0.0.1:8000/api/token/refresh/',
            null,
            {
              headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
              }
            }
          );
          console.log(response);
          // Установите новый токен в localStorage
          localStorage.setItem('accessToken', response.data.access);
          localStorage.setItem('refreshToken', response.data.refresh);
        }
      } catch (error) {
        console.error(error);
      }
    },

    logout() {
      localStorage.removeItem('accessToken');
      localStorage.removeItem('refreshToken');
      this.username = null;
      this.$router.push({ name: 'Login' });
    }
  },
};

</script>